@use 'variables' as v; //Alias

h1, h2, h3{
    font-family: v.$fuentePrincipal;
    margin: 0 0 v.$separacion 0 ;   
    text-transform: uppercase;
    text-align: center;
    line-height: 1.5;
}

h1{
    font-size: 4rem;
}

h2{
    font-size: 3.6rem;
    color: v.$secundario;
    
    span{
        color: v.$primario;
        font-size: 1.8rem; 
        display: block;
    }

    &::after{
        content: '';
        display: block;
        width: 10rem;
        height: 10rem;
        margin: 0 auto;
        background-image: url(../img/cafe.svg);
        background-size: 10rem;
    }
}

.heading-blanco{
    @extend h2;
    color: v.$blanco;

    span{
        color: v.$blanco;
    }

    &::after{
        display: none;
    }
}

h3{
    font-size: 3.2rem;
}

p{
    color: v.$negro;
    font-size: 2rem;
}
